'use client';

import { useEffect, useState } from 'react';

import { motion } from 'motion/react';

import '@/components/Hero.css';
import { getHomeInfo } from '@/lib/get_home_info';

const banner = {
  animate: {
    transition: {
      delayChildren: 0.4,
      staggerChildren: 0.1,
    },
  },
};

const letterAnimation = {
  initial: { y: 400 },
  animate: {
    y: 0,
    transition: {
      ease: [0.6, 0.01, -0.05, 0.95],
      duration: 1,
    },
  },
};

export const Hero = () => {
  const [homeInfo, setHomeInfo] = useState(null);

  // Cargar la información de la API en el cliente
  useEffect(() => {
    const fetchData = async () => {
      try {
        const data = await getHomeInfo();
        setHomeInfo(data);
      } catch (error) {
        console.error('Error loading home info:', error);
      }
    };

    fetchData();
  }, []);

  if (!homeInfo) {
    return <div>Loading...</div>; // Puedes personalizar el loading según lo desees
  }

  const {
    encabezado_1,
    encabezado_2,
    encabezado_3,
    descripcion,
    tituloMultimedia,
    video,
  } = homeInfo;

  return (
    <section className="pt-10 mx-auto relative flex flex-col border-black border-2">
      <div className="border-slate-700 border-2 banner">
        <BannerRowTop title={encabezado_1} />
      </div>
      <p className="absolute text-xl top-16 right-24 max-w-[380px]">
        {descripcion}
      </p>

      <video
        className="mt-24 rounded-lg aspect-video w-[840px] self-center"
        controls
      >
        <source src={video} type="video/mp4" className="w-full aspect-video" />
      </video>
    </section>
  );
};

const SimpleComponent = () => (
  <motion.div
    animate={{ scale: 1 }}
    initial={{ scale: 0 }}
    transition={{ duration: 0.5 }}
  >
    Simple Animation
  </motion.div>
);

const AnimatedLetters = ({
  title,
  disabled,
}: {
  title: string;
  disabled?: boolean;
}) => <SimpleComponent />;

const BannerRowTop = ({ title }: { title: string }) => {
  return (
    <div className={'banner-row'}>
      <div className="row-col">
        <AnimatedLetters title={title} />
      </div>
    </div>
  );
};

const BannerRowBottom =
  ({ title }: { title: string }) =>
  () => {
    return (
      <div className={'banner-row center'}>
        <motion.div
          initial={{ scale: 0 }}
          animate={{ scale: 1 }}
          transition={{ ease: [0.6, 0.01, -0.05, 0.95], duration: 1, delay: 1 }}
          className="scroll"
        >
          <motion.span
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            transition={{
              ease: 'easeInOut',
              duration: 1,
              delay: 1.8,
            }}
          >
            scroll
          </motion.span>
          <motion.span
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            transition={{
              ease: 'easeInOut',
              duration: 1,
              delay: 1.8,
            }}
          >
            down
          </motion.span>
        </motion.div>
        <AnimatedLetters title={title} />
      </div>
    );
  };

const BannerRowCenter = ({ title }: { title: string }) => {
  return (
    <div className={`banner-row marquee`}>
      <motion.div
        initial={{ y: 310 }}
        animate={{ y: 0 }}
        transition={{ ease: [0.6, 0.01, -0.05, 0.9], duration: 1 }}
        className="marquee__inner"
      >
        <AnimatedLetters title={title} disabled />
        <AnimatedLetters title={title} />
        <AnimatedLetters title={title} disabled />
        <AnimatedLetters title={title} disabled />
      </motion.div>
    </div>
  );
};
